<template>
  <v-container
    id="regular-tables-view"
    fluid
    tag="section"
  >
    <material-card
      icon="mdi-clipboard-text"
      icon-small
      color="primary"
      title="Gestin de Kardex"
    >
      <v-data-table
        :headers="headers"
        :items="lista_kardex"
        sort-by=""
        class="elevation-1"
        :loading="cargando"
        loading-text="Cargando... Espere por favor"
        no-data-text="No existen datos"
        no-results-text="No se encontraron registros coincidentes"
        :search="search"
        :footer-props="{
          showFirstLastPage: true,
          firstIcon: 'mdi-arrow-collapse-left',
          lastIcon: 'mdi-arrow-collapse-right',
          prevIcon: 'mdi-arrow-left',
          nextIcon: 'mdi-arrow-right',
          'items-per-page-text':'Items por pgina'
        }"
      >
        <template v-slot:top>
          <v-toolbar
            flat
          >
            <v-text-field
              v-model="search"
              append-icon="mdi-magnify"
              label="Bsqueda"
              single-line
              hide-details
            />
            <v-spacer />
            <v-dialog
              v-model="dialog"
              max-width="1000px"
            >
              <!-- <template v-slot:activator="{ on, attrs }">
                <v-btn
                  color="primary"
                  dark
                  class="mb-2"
                  v-bind="attrs"
                  v-on="on"
                >
                  Nuevo Registro
                </v-btn>
              </template> -->
              <v-card>
                <v-card-title>
                  <span class="text-h5">{{ formTitulo }}</span>
                </v-card-title>
                <v-card-text>
                  <v-form ref="form" v-model="valid">
                    <!-- {{kardex}} -->
                    <v-container>
                      <v-row>
                        <v-col
                          cols="12"
                          sm="6"
                          md="4"
                        >
                          <v-text-field
                            v-model="kardex.numeroKardex"
                            label="Nmero de Kardex"
                            hint="Introducir nmero"
                            :counter="10"
                            persistent-hint
                            :autofocus="true"
                          />
                        </v-col>
                        <v-col
                          cols="12"
                          sm="6"
                          md="4"
                        >
                          <v-autocomplete
                            v-model="kardex.distritoId"
                            :items="lista_distrito"
                            item-text="descripcion"
                            item-value="id"
                            label="Departamento"
                            hint="Seleccione el departamento"
                            persistent-hint
                            no-data-text="No existen datos"
                          >
                          </v-autocomplete>
                        </v-col>
                        <v-col
                          cols="12"
                          sm="6"
                          md="4"
                        >
                          <v-autocomplete
                            v-model="kardex.profesionId"
                            :items="lista_profesion"
                            item-text="descripcion"
                            item-value="id"
                            label="Profesin"
                            hint="Seleccione la profesin"
                            persistent-hint
                            no-data-text="No existen datos"
                          >
                          </v-autocomplete>
                        </v-col>
                        <v-col
                          cols="12"
                          sm="6"
                          md="3"
                        >
                          <v-text-field
                            v-model="kardex.numeroInscripcion"
                            label="N de Inscripcin"
                            hint="Matricula Departamental"
                            :counter="20"
                            :rules="fieldRules"
                            persistent-hint
                          />
                        </v-col>
                        <v-col
                          cols="12"
                          sm="6"
                          md="3"
                        >
                          <v-text-field
                            v-model="kardex.numeroMinisterio"
                            label="N de Registro Ministerio"
                            hint="Introducir matricula profesional"
                            :counter="20"
                            persistent-hint
                          />
                        </v-col>
                        <v-col
                          cols="12"
                          sm="6"
                          md="6"
                        >
                          <v-text-field
                            v-model="kardex.lugarTrabajo"
                            label="Lugar Trabajo"
                            hint="Introducir ente"
                            :counter="100"
                            persistent-hint
                          />
                        </v-col>
                      </v-row>
                    </v-container>
                  </v-form>
                </v-card-text>
                <v-card-actions>
                  <v-spacer />
                  <v-btn
                    text
                    @click="close"
                  >
                    Cancelar
                  </v-btn>
                  <v-btn
                    :loading="estadoBoton"
                    text
                    @click="guardar"
                    :disabled="!valid"
                  >
                    Guardar
                  </v-btn>
                </v-card-actions>
              </v-card>
            </v-dialog>


            <v-dialog
              v-model="dialogDetalle"
              max-width="500px"
            >
              <v-card>
                <v-card-title>
                  <span class="text-h4">Detalle de Kardex</span>
                </v-card-title>
                <v-card-text>
                  <div class="text-overline mb-1" style="color: black">
                    <p class="text-h6">N Kardex: {{kardex.numeroKardex}}</p>
                    <p class="text-h6">Carnet Identidad: {{kardex.Persona.ci}}</p>
                    <p class="text-h6">Nombre Completo: {{kardex.Persona.nombre}} {{kardex.Persona.paterno}} {{kardex.Persona.materno}}</p>
                    <!-- <p class="text-h6">Apellido Esposo: {{kardex.Persona.ApellidoEsposo}}</p> -->
                    <p class="text-h6">Departamento: {{kardex.Distrito.descripcion}}</p>
                    <p class="text-h6">Profesin: {{kardex.Profesion.descripcion}}</p>
                    <p class="text-h6">Matrcula Colegio: {{kardex.numeroInscripcion}}</p>
                    <p class="text-h6">Matrcula Profesional: {{kardex.numeroMinisterio}}</p>
                    <p class="text-h6">Lugar de Trabajo: {{kardex.lugarTrabajo}}</p>
                    
                  </div>
                </v-card-text>
                <v-card-actions>
                  <v-spacer />
                  <v-btn
                    text
                    @click="closeDetalle"
                  >
                    Cerrar
                  </v-btn>
                </v-card-actions>
              </v-card>
            </v-dialog>


            <v-dialog
              v-model="dialogTraspaso"
              max-width="1000px"
            >
              <v-card>
                <v-card-title>
                  <span class="text-h5">Traspaso de Afiliado</span>
                </v-card-title>
                <v-card-text>
                  <v-form ref="form" v-model="valid">
                    <v-container>
                      <v-row>
                        <v-col
                          cols="12"
                          sm="6"
                          md="4"
                        >
                          <v-text-field
                            v-model="kardex.numeroKardex"
                            label="Nmero de Kardex"
                            hint="Introducir nmero"
                            :counter="10"
                            persistent-hint
                            :autofocus="true"
                          />
                        </v-col>
                        <v-col
                          cols="12"
                          sm="6"
                          md="4"
                        >
                          <v-autocomplete
                            v-model="kardex.distritoId"
                            :items="lista_distrito"
                            item-text="descripcion"
                            item-value="id"
                            label="Departamento"
                            hint="Seleccione el departamento"
                            persistent-hint
                            no-data-text="No existen datos"
                          >
                          </v-autocomplete>
                        </v-col>
                        <v-col
                          cols="12"
                          sm="6"
                          md="4"
                        >
                          <v-autocomplete
                            v-model="kardex.profesionId"
                            :items="lista_profesion"
                            item-text="descripcion"
                            item-value="id"
                            label="Profesin"
                            readonly
                            persistent-hint
                            no-data-text="No existen datos"
                          >
                          </v-autocomplete>
                        </v-col>
                        <v-col
                          cols="12"
                          sm="6"
                          md="3"
                        >
                          <v-text-field
                            v-model="kardex.numeroInscripcion"
                            label="N de Inscripcin"
                            readonly
                            persistent-hint
                          />
                        </v-col>
                        <v-col
                          cols="12"
                          sm="6"
                          md="3"
                        >
                          <v-text-field
                            v-model="kardex.numeroMinisterio"
                            label="N de Registro Ministerio"
                            readonly
                            persistent-hint
                          />
                        </v-col>
                        <v-col
                          cols="12"
                          sm="6"
                          md="6"
                        >
                          <v-text-field
                            v-model="kardex.lugarTrabajo"
                            label="Lugar Trabajo"
                            hint="Introducir ente"
                            :counter="100"
                            persistent-hint
                          />
                        </v-col>
                      </v-row>
                    </v-container>
                  </v-form>
                </v-card-text>
                <v-card-actions>
                  <v-spacer />
                  <v-btn
                    text
                    @click="closeTraspaso"
                  >
                    Cancelar
                  </v-btn>
                  <v-btn
                    :loading="estadoBoton"
                    text
                    @click="guardarTraspaso"
                    :disabled="!valid"
                  >
                    Guardar Traspaso
                  </v-btn>
                </v-card-actions>
              </v-card>
            </v-dialog>


            <v-dialog
              v-model="dialogDelete"
              max-width="500px"
            >
              <v-card>
                <v-card-title class="text-h5">
                  Est seguro de eliminar el registro?
                </v-card-title>
                <v-spacer />
                <v-card-subtitle class="text-h6">
                  {{ kardex.Persona.ci + ' - ' + kardex.Persona.paterno + ' ' + kardex.Persona.materno + ' ' + kardex.Persona.nombre }}
                </v-card-subtitle>
                <v-card-actions>
                  <v-spacer />
                  <v-btn
                    color="blue darken-1"
                    text
                    @click="closeEliminar"
                  >
                    Cancelar
                  </v-btn>
                  <v-btn
                    color="red darken-1"
                    text
                    @click="eliminarConfirm"
                  >
                    Eliminar
                  </v-btn>
                </v-card-actions>
              </v-card>
            </v-dialog>
          </v-toolbar>
        </template>
        <template v-slot:[`item.acciones`]="{ item }">
          <v-btn
            class="ma-1"
            fab
            dark
            x-small
            color="primary darken-2"
            @click="detalle(item)"
          >
            <v-icon
              dark
              small
              class="ma-2"
            >
              mdi-text
            </v-icon>
          </v-btn>
          <v-btn
            class="ma-1"
            fab
            dark
            x-small
            color="primary"
            @click="traspaso(item)"
          >
            <v-icon
              dark
              small
              class="ma-2"
            >
              mdi-swap-horizontal
            </v-icon>
          </v-btn>
          <v-btn
            class="ma-1"
            fab
            dark
            x-small
            color="yellow darken-3"
            @click="editar(item)"
          >
            <v-icon
              dark
              small
              class="ma-2"
            >
              mdi-pencil
            </v-icon>
          </v-btn>
          <v-btn
            class="ma-1"
            fab
            dark
            x-small
            color="red darken-2"
            @click="eliminar(item)"
          >
            <v-icon
              dark
              small
              class="ma-2"
            >
              mdi-delete
            </v-icon>
          </v-btn>
        </template>
      </v-data-table>
    </material-card>
    <v-snackbar
      v-model="snackbar"
      :timeout="timeout"
      :color="color"
    >
      {{ text }}

      <template v-slot:action="{ attrs }">
        <v-btn
          color=""
          text
          v-bind="attrs"
          @click="snackbar = false"
        >
          Cerrar
        </v-btn>
      </template>
    </v-snackbar>
  </v-container>
</template>

<script>
import * as profesionService from "../services/profesionService"
import * as personaService from "../services/personaService"
import * as kardexService from "../services/kardexService"
import * as distritoService from "../services/distritoService"

  export default {
    data () {
      return {
        estadoBoton: false,
        dialog: false,
        dialogDelete: false,
        dialogDetalle: false,
        dialogTraspaso: false,
        lista_kardex: [],
        lista_distrito: [],
        lista_profesion: [],
        lista_jornada: [
          'Medio Tiempo', 'Tiempo Completo', 'Dedicacin Exclusiva'
        ],
        cargando: true,
        kardex: {
          Persona: {ci:'', paterno:'', materno:'', nombre:''},
          Profesion: {},
          Distrito: {}
          // descripcion: '',
          // categorias: [
          //   { categoriaId: 45 },
          // ],
        },
        search: '',
        headers: [
          {
            text: 'ID',
            align: 'start',
            sortable: false,
            value: 'id',
          },
          { text: 'Departamento', value: 'Distrito.descripcion' },
          { text: 'N K.', value: 'numeroKardex' },
          { text: 'C.I.', value: 'Persona.ci' },
          { text: 'Nombre Completo', value: 'Persona.nombreCompleto' },
          { text: 'Profesin', value: 'Profesion.descripcion' },
          { text: 'Mat. Col.', value: 'numeroInscripcion' },
          { text: 'Mat. Prof.', value: 'numeroMinisterio' },
          { text: 'Vigente', value: 'vigente' },
          { text: 'Acciones', value: 'acciones', sortable: false, align: 'center' },
        ],
        editedIndex: -1,
        defaultKardex: {
          Persona: {ci:'', paterno:'', materno:'', nombre:''},
          Distrito: {},
          Profesion: {},
        },
        color: '',
        snackbar: false,
        text: '',
        timeout: 3000,
        valid: true,
        fieldRules: [
          v => !!v || 'El campo es requerido',
          v => (v && v.length <= 50) || 'El campo debe ser menor a 50 caracteres',
        ],
        menu1: false,
        menu2: false,
        menu3: false,
      }
    },
    computed: {
      formTitulo () {
        return this.editedIndex === -1 ? 'Nuevo Registro' : 'Editar Registro'
      },
    },
    watch: {
      dialog (val) {
        val || this.close()
      },
      dialogDelete (val) {
        val || this.closeEliminar()
      },
      dialogDetalle (val) {
        val || this.closeDetalle()
      },
      dialogTraspaso (val) {
        val || this.closeTraspaso()
      },
    },
    async mounted () {
      // let datos = await kardexService.listar()
      // console.log(datos);
      // this.lista_kardex = datos
      // this.cargando = false
      this.obtener()
    },
    methods: {
      async obtener () {
        const res = await kardexService.listar()
        // console.log(datos)
        this.lista_kardex = res.data
        this.cargando = false
      },
      async obtenerDistrito () {
        const res = await distritoService.listar()
        // console.log(res)
        this.lista_distrito = res.data
        this.cargando = false
      },
      async obtenerProfesion () {
        const res = await profesionService.listar()
        // console.log(res)
        this.lista_profesion = res.data
        this.cargando = false
      },
      close () {
        this.dialog = false
        this.$nextTick(() => {
          this.kardex = Object.assign({}, this.defaultKardex)
          this.editedIndex = -1
        })
        this.estadoBoton = false
      },
      closeDetalle () {
        this.dialogDetalle = false
        this.$nextTick(() => {
          this.kardex = Object.assign({}, this.defaultKardex)
          this.editedIndex = -1
        })
        this.estadoBoton = false
      },
      closeTraspaso () {
        this.dialogTraspaso = false
        this.$nextTick(() => {
          this.kardex = Object.assign({}, this.defaultKardex)
          this.editedIndex = -1
        })
        this.estadoBoton = false
      },
      async guardar () {
        let res = null
        this.validate();
        this.estadoBoton = true
        if (this.editedIndex > -1) {
          const mar = this.lista_kardex[this.editedIndex]
          res = await kardexService.modificar(this.kardex, mar.id)
          Object.assign(this.lista_kardex[this.editedIndex], this.kardex)
        } else {
          res = await kardexService.guardar(this.kardex)
          // console.log(res);
          this.lista_kardex.push(this.kardex)
        }
        if(res.status == 200){
          this.text = "Registro guardado"
          this.snackbar = true
          this.color = "green"
        } else {
          this.text = res.data.message
          this.snackbar = true
          this.color = "dark-red"
        }
        this.obtener()
        this.estadoBoton = false
        this.close()
      },
      async guardarTraspaso () {
        let res = null
        this.validate();
        this.estadoBoton = true
        if (this.editedIndex > -1) {
          const mar = this.lista_kardex[this.editedIndex]
          res = await kardexService.guardarTraspaso(this.kardex, mar.id)
          Object.assign(this.lista_kardex[this.editedIndex], this.kardex)
        } else {
          console.log('Algo salio mal');
        }
        if(res.status == 200){
          this.text = "Traspaso guardado"
          this.snackbar = true
          this.color = "green"
        } else {
          this.text = res.data.message
          this.snackbar = true
          this.color = "dark-red"
        }
        this.obtener()
        this.estadoBoton = false
        this.closeTraspaso()
      },
      editar (item) {
        this.editedIndex = this.lista_kardex.indexOf(item)
        this.kardex = Object.assign({}, item)
        this.dialog = true
        this.obtenerDistrito()
        this.obtenerProfesion()
      },
      detalle (item) {
        this.editedIndex = this.lista_kardex.indexOf(item)
        this.kardex = Object.assign({}, item)
        this.dialogDetalle = true
      },
      traspaso (item) {
        this.editedIndex = this.lista_kardex.indexOf(item)
        this.kardex = Object.assign({}, item)
        delete this.kardex.id
        this.dialogTraspaso = true
        this.obtenerDistrito()
        this.obtenerProfesion()
      },
      eliminar (item) {
        this.editedIndex = this.lista_kardex.indexOf(item)
        this.kardex = Object.assign({}, item)
        this.dialogDelete = true
      },
      async eliminarConfirm () {
        try {
          const mar = this.lista_kardex[this.editedIndex]
          const res = await kardexService.eliminar(mar.id)
          if(res.status == 200){
            this.text = res.data.message
            this.snackbar = true
            this.color = "green"
            this.lista_kardex.splice(this.editedIndex, 1)
          } else {
            this.text = res.data.message
            this.snackbar = true
            this.color = "dark-red"
          }
          // if (!res.error) {
          //   this.lista_kardex.splice(this.editedIndex, 1)
          // }
          this.closeEliminar()
        } catch (error) {
          console.log(error);
        }
      },
      closeEliminar () {
        this.dialogDelete = false
        this.$nextTick(() => {
          this.kardex = Object.assign({}, this.defaultKardex)
          this.editedIndex = -1
        })
      },
      validate() {
        this.$refs.form.validate()
      }
    },
  }
</script>
